  background-color: #f9f9f9;
  color: #222222;
  height: 100%; }

body {
  height: 100%; }

p {
  font-size: .75em;
  line-height: 1.5em; }

em {
  font-weight: bold; }

a {
  text-decoration: none; }
  a:hover {
    text-decoration: underline; }

h1 {
  text-align: center;
  margin-bottom: 60px; }

/*================================
=            HEADINGS            =
================================*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold; }

html {
  font-size: calc(112.5% + 0.25vw); }

h1 {
  font-size: 2em; }

h2 {
  font-size: 1.5em; }

h3 {
  font-size: 1.17em; }

h4 {
  font-size: 1.12em; }

h5 {
  font-size: .83em; }

