/*================================
=            WRAPPERS            =
================================*/
.o-site-container {
  min-height: 100%;
  padding-top: 80px; }

.o-site-content {
  width: 100%; }

.o-layout-container {
  padding: 0 20px;
  max-width: 1000px;
  margin: 0 auto; }

.o-layout-container--slim {
  padding: 0 20px;
  max-width: 36em;
  margin: 0 auto; }

/*================================
=            HEADLINE            =
================================*/
.o-headline {
  margin-bottom: 10px;
  margin-bottom: rem(10px); }

.o-headline__main,
.o-headline__sub {
  margin-bottom: 5px;
  margin-bottom: rem(5px);
  line-height: 1; }

.o-logo {
  float: left;
  padding: 10px 0; }
  .o-logo img {
    max-width: 60px;
    display: block; }

.o-logo-header {
  max-width: 100%;
  width: 425px; }

.o-section-background {
  position: relative;
  padding: 60px 0 20px 0;
  color: #FFFFFF;
  text-align: center;
  overflow: hidden; }
  .o-section-background .c-content {
    margin-left: auto;
    margin-right: auto; }
  .o-section-background:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(57, 84, 109, 0.75); }
  .o-section-background * {
    z-index: 1;
    position: relative; }

.o-social a {
  color: #FFFFFF;
  margin-right: 10px; }
  .o-social a:hover {
    color: #C3C354;
    text-decoration: none; }

/**
 * COMPONENTS
 *
 * - Components are implementation-specific bits of UI.
 * - Format: .c-component-name[<element>|<modifier>] {}
 *
 */
.c-footer {
  padding: 40px;
  background-color: #222222;
  color: #FFFFFF;
  overflow: hidden; }
  .c-footer p {
    margin-bottom: 0;
    float: left; }
  .c-footer .o-social {
    float: right; }

.c-page-header {
  margin-bottom: 20px; }
  .c-page-header h1 {
    margin-bottom: 0; }

.c-showcase {
  max-width: 740px;
  margin-bottom: 20px; }

.c-showcase__wrapper {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  height: 0; }
  .c-showcase__wrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

.c-content {
  max-width: 36em;
  margin-bottom: 60px; }

.c-content--background {
  position: relative;
  padding: 40px 0; }
  .c-content--background:after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4); }

@media (min-width: 40.063em) {
  .c-content--two-col {
    display: flex; }
    .c-content--two-col .c-content__column {
      width: 48.33333%; }
      .c-content--two-col .c-content__column:last-of-type {
        margin-left: 3.33333%; } }

.c-header {
  background-color: #39546d;
  overflow: hidden; }
  .c-header .o-social {
    display: flex;
    align-items: center; }

.c-header__wrapper {
  display: flex;
  justify-content: space-between; }

.c-nav {
  color: #FFFFFF;
  margin: 0;
  margin-left: auto;
  margin-right: 20px;
  overflow: hidden; }
  @media (min-width: 40.063em) {
    .c-nav {
      margin-right: 40px; } }
  .c-nav li {
    height: 100%;
    list-style: none;
    float: left; }
  .c-nav a {
    display: flex;
    align-items: center;
    height: 100%;
    color: #FFFFFF;
    font-size: 18px;
    transition: background-color .2s ease;
    padding: 10px; }
    @media (min-width: 40.063em) {
      .c-nav a {
        padding: 10px 20px; } }
    .c-nav a:hover {
      text-decoration: none;
      background-color: #222222; }

.c-featured {
  text-align: center;
  margin-bottom: 80px; }

.c-featured__image {
  margin-bottom: 20px;
  width: 600px;
  max-width: 100%; }

.c-featured__title {
  margin-bottom: 10px; }

.c-featured__app-links {
  display: flex;
  justify-content: center;
  width: 450px;
  max-width: 100%;
  margin: 0 auto; }
  .c-featured__app-links img {
    max-width: 150px; }
    @media (min-width: 40.063em) {
      .c-featured__app-links img {
        max-width: 200px; } }

.c-person {
  padding: 20px 0;
  margin: 20px 40px 0;
  height: 250px;
  transition: transform 1s;
  transform-style: preserve-3d;
  cursor: pointer;
  position: relative; }
  .c-person.is-flipped {
    transform: rotateY(180deg); }
  .c-person p {
    margin-bottom: 10px;
    color: #222222; }

.c-person__image {
  width: 100px; }

.c-person__card {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden; }

.c-person__card--front {
  background: #FFFFFF;
  transition: background-color .2s ease; }
  .c-person__card--front:hover {
    background-color: rgba(255, 255, 255, 0.8); }
  .c-person__card--front p {
    font-size: 18px;
    line-height: 1;
    font-weight: bold; }

.c-person__card--back {
  background: #222222;
  transform: rotateY(180deg);
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  padding: 20px; }
  .c-person__card--back p {
    color: #FFFFFF; }
  .c-person__card--back span {
    display: block;
    margin-right: 10px;
    color: #C3C354; }

