/*================================
=            HEADINGS            =
================================*/

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
}

html { font-size: calc(112.5% + 0.25vw) }

h1 { font-size: 2em; }
h2 { font-size: 1.5em; }
h3 { font-size: 1.17em; }
h4 { font-size: 1.12em; }
h5 { font-size: .83em; }
h6 { font-size: .75em; }
